<template>
    <div>
        <el-card class="box-card">
            <span>ps：YT5329023693378</span>
          <el-form :model="form">
            <el-form-item label="请收入快递单号">
              <el-input v-model="form.info"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button @click="serch">查询</el-button>
            </el-form-item>
          </el-form>


           <el-timeline>
            <el-timeline-item
              v-for="(activity, index) in activities"
              :key="index"
              :timestamp="activity.time"
              :icon="activity.icon"
              :type="activity.type"
              :color="activity.color"
              :size="activity.size">
              {{activity.status}}
            </el-timeline-item>
          </el-timeline>
        </el-card>

    </div>
</template>
<script>
  export default {
    data() {
      return {
        form:{
          info:''
        },
        activities:[],
      }
    },
    methods:{
      async serch(){
        const res = await this.$http.get(`expressInfo?no=${this.form.info}`)
        let lengths = res.data.result.list.length-1
        let item = res.data.result.list.map(e =>{
          return e
        })
        item[lengths]['icon'] = 'el-icon-office-building'
        item[0]['icon'] = 'el-icon-truck'
        this.activities = item
      }
    },
  }
</script>
<style lang="less" scoped>
/deep/.el-timeline-item__node--normal{
  background: #2196F3;
  width: 25px;
  height: 25px;
  left: -7px;
}
</style>